*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --main-white: #ffffff;
    --main-red: #cf3a59;
    --main-blue: #081a51;
    --main-background: #f8f9ff;
    --main-lines: #dadada;
    --btn-blue: #017efa;
    --font-blue: #51cbff;
    --help-box: #1b2b65;
    --perma-white: #ffffff;
}
* {
    font-family: "GilroyMedium", sans-serif;
    margin: 0;
    padding: 0;
    border: 0;
}

@font-face {
    font-family: "GilroyThin";
    src: url(../fonts/Gilroy-Thin.ttf);
}

ul,
ol,
li {
    list-style: none;
}
body {
    font-family: "GilroyMedium", sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4;
    margin: 0;
    color: var(--main-white);
}

#navbar-container {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;

    width: 300px;
    /* height: 100vh; */
    background-color: var(--main-blue);
}

.logo-container {
    display: flex;
    flex-basis: 100%;
    width: 290px;
    text-align: center;
    align-items: center;
    font-size: 0.9rem;
    margin: 0.3rem 0;
    cursor: pointer;
}

.home-link {
    text-decoration: none;
    color: white;
}
#logo-img {
    width: 55px;
    height: 55px;
    margin-left: 10px;
}

#logo-title {
    padding: 1rem 5px;
}

.section-title {
    font-family: "GilroyThin";
    font-size: 16px;
    margin: 2rem 2.5rem 0 2rem;
    color: var(--font-blue);
}

.li-container {
    display: flex;
    justify-content: left;
    align-items: center;
    width: 100%;
    height: 56px;
    border-radius: 5px;
    padding: 20px;
    background-color: var(--main-blue);
}

.li-container:hover {
    background-color: var(--help-box);
    transition: background-color 0.2s ease-out;
}

#story-counter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: auto;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    border-radius: 4px;
    background-color: var(--main-red);
}

.help-container {
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 80%;
    margin: 1.75rem 2rem;
    padding: 2rem 0 1rem 0;
    border-radius: 8px;
    background-color: var(--help-box);
}

.main-btn {
    width: 80%;
    height: 40px;
    margin: 1rem 0 1rem 0;
    border-radius: 6px;
    font-family: "GilroyMedium", sans-serif;
    font-size: 18px;
    background-color: var(--btn-blue);
    color: white;
    box-shadow: 0 0 40px 40px var(--btn-blue) inset, 0 0 0 0 var(--btn-blue);
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.main-btn:hover {
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
    transition: transform 0.3s ease-out;
    cursor: pointer;
}

#question-icon {
    position: absolute;
    /* top: 0; */
    margin-bottom: 11.5rem;
    width: 100px;
}

#help-text {
    margin: 1rem 0 0 0;
    font-size: 1.2rem;
    text-align: center;
    color: var(--perma-white);
}

@media (max-width: 768px) {
    #navbar-container {
        /* position: absolute; */
        display: none;
    }
}
