*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --main-white: #ffffff;
    --main-red: #cf3a59;
    --main-blue: #081a51;
    --main-background: #f8f9ff;
    --main-lines: #dadada;
    --btn-blue: #017efa;
    --font-blue: #51cbff;
}

@font-face {
    font-family: "GilroySemiBold";
    src: url(../fonts/Gilroy-SemiBold.ttf);
}

@font-face {
    font-family: "GilroyMedium";
    src: url(../fonts/Gilroy-Medium.ttf);
}

@font-face {
    font-family: "GilroyLight";
    src: url(../fonts/Gilroy-Light.ttf);
}
body {
    font-family: "GilroySemiBold", sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4;
    margin: 0;
}

.newPostAndtags-container {
    position: fixed;
    top: 40%;
    left: 45%;
    transform: translate(-50%, -50%);
    width: 50vw;
    margin: 2.5rem 5rem;
    z-index: 1000;
}

.newPost-tags-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    height: 20px;
    margin-bottom: 1rem;
}
.newPost-container {
    display: flex;
    flex-direction: column;
    background-color: var(--main-post);
    color: var(--font-black);
    border-radius: 8px;
    box-shadow: 0px 5px 10px var(--main-shadow);
    min-width: 650px;
}
.new-post-content-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 400px;
    min-width: 350px;
    padding-bottom: 2rem;
}

#new-post-title {
    padding: 2rem 4rem;
    font-size: 2rem;
    outline: none;
    background-color: var(--main-post);
}

.newPost-content {
    text-align: justify;
    font-size: 1.2rem;
    height: auto;
    font-family: "GilroyMedium";
    padding: 0 4rem;
    background-color: var(--main-post);
}
#new-post-content {
    display: flex;
    min-height: fit-content;
    width: 100%;
    min-height: 200px;
    margin: 0 2rem 0 0;
    line-height: 2rem;
    outline: none;
    resize: none;
    /* margin-top: -10rem; */
}

#button-quit-post {
    height: 60px;
    margin-top: 0.5rem;
    margin-right: -2rem;
    cursor: pointer;
}

.switch-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 4rem;
    font-family: "GilroyMedium";
}

#switch-wrapper {
    display: flex;
    align-items: center;
    /* min-width: 250px; */
    margin-right: 2rem;
}
.switch {
    position: relative;
    display: inline-block;
    min-width: 60px;
    height: 34px;
    margin-right: 1rem;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #2196f3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.newStory-main-btn {
    width: 80%;
    max-width: 280px;
    height: 40px;
    margin: 1rem 0 1rem 0;
    border-radius: 6px;
    font-family: "GilroyMedium", sans-serif;
    font-size: 18px;
    background-color: var(--btn-blue);
    color: white;
    box-shadow: 0 0 40px 40px var(--btn-blue) inset, 0 0 0 0 var(--btn-blue);
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.newStory-main-btn:hover {
    box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
    transition: transform 0.3s ease-out;
    cursor: pointer;
}
